import React from 'react';
import Standalone from '../layouts/standalone';
import ListingDetails from '../listing-details';
import Map from '../map';
import Disqus from 'disqus-react';
import Share from '../share'


const StandardListing = (props) => {
  console.log(props);
  const { title, image, coordinates, tags, ...details } = props.data.markdownRemark.frontmatter;
  const content = props.data.markdownRemark.html;
  const { googleMapsKey } = props.data.site.siteMetadata;
  return (<Standalone>
    <main className="listing">
      <div className="container-fluid">
        <div className="row">
          <div className="col-md">
            <article className="card">
              {image && (
                <img src={image} className="card-img-top" alt={title}/>
              )}
              <div className="card-body">
                <h1 className="title--header">{title}</h1>
                {/* TODO Icons */}
                <div dangerouslySetInnerHTML={{ __html: content }} />
                <ListingDetails { ...details }></ListingDetails>
                <Map coordinates={coordinates} googleMapsKey={googleMapsKey}></Map>
              </div>
              <ul className="list-group list-group-flush m-0">
                <li className="list-group-item"><em className="tag-list">Tags: {tags.join(', ')}</em></li>
              </ul>
            </article>
            <Share/>
            <Disqus.DiscussionEmbed shortname="expatty" />
          </div>
        </div>
      </div>
    </main>
  </Standalone>)
};

export default StandardListing;

export const pageQuery = graphql`
    query BlogPostBySlug($slug: String!) {
        site {
            siteMetadata {
                googleMapsKey
            }
        }
        markdownRemark(fields: { slug: { eq: $slug } }) {
            id
            html
            frontmatter {
                title
                image
                website
                tel
                email
                address
                price
                coordinates
                tags
            }
        }
    }
`